import React from 'react';
import Tilt from 'react-tilt';
import brain from './brain.png';
import './Logo.css';

const Logo = () => {
    return (
        <div className='ma4 mt0'>
            <Tilt className='Tilt br shadow-1' options={{ max : 55 }} style={{ height: 250, width: 250 }} >
                <div className='Tilt-inner'>
                    <img style={{paddingTop: '70px'}} alt='pfungwa' src={brain} /> </div>
            </Tilt>
        </div>
    );

}

export default Logo;